body {
  margin: 0;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translate(0, -50%);
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin: 0 auto;
  animation: 3600s steps(60, end) infinite clock-work;
  position: absolute;
  bottom: 50%;
  left: 0;
  right: 0;
}

.stopwatch__minute-hand--speed-up {
  animation-duration: 600s;
}

.stopwatch__second-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin: 0 auto;
  animation: 60s linear infinite clock-work;
  position: absolute;
  bottom: 50%;
  left: 0;
  right: 0;
}

.stopwatch__second-hand--speed-up {
  animation-duration: 10s;
}

@keyframes clock-work {
  to {
    transform: rotate(360deg);
  }
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translate(0, -50%);
}
